import { useNavigate } from "react-router-dom";
import AuthService from "../services/Auth.service";
import MicrosoftLogin from "react-microsoft-login";
const Login = () =>{
  
  const navigate = useNavigate();


  window.handleCredentialResponse = async (response) => {
    console.log(response);
  }
  
  const authHandler = (err, data,msal) => {
    console.log(err, data);
    if(!err) {
      AuthService.microsoftLogin(data);
      navigate('/dashboard');
    }
  }; 

  return (
    <>
      <div className="container-fluid login-wrapper">
          <div className="login-box">
              <h1 className="text-center mb-5"><i className="fa fa-rocket text-primary"></i> RMS Portal</h1>  
              <div className="row">
                <div className="col-sm-4"></div>
                <div className="col-sm-4">
                  <MicrosoftLogin clientId="ac65c467-ef84-4109-9001-dbce87af624e" authCallback={authHandler} />
                </div>
                <div className="col-sm-4"></div>
              </div>
          </div>
      </div>
    </>
  );
}

export default Login;